import { Fragment, useState, useContext } from "react";
import { Link } from "react-router-dom";
import AuthContext from "../../store/authContexts";

import classes from "./header.module.css";

const Header = () => {
    const [ headerState, setHeaderState ] = useState(false)

    const authCTX = useContext(AuthContext)

    const toggleHeader = () => {
        setHeaderState(prevState=>{
            return !prevState
        })
    }

    const closeHeader = () => {
        setHeaderState(false)
    }
  return (
    <Fragment>
      <header className={classes["dev-main-header"]}>
        <div onClick={toggleHeader} className={`${classes["burger-menu-container"]} ${headerState?classes["opened"]:""}`}>
          <span className={classes["burger-solid-element"]}></span>
          <span className={classes["burger-solid-element"]}></span>
          <span className={classes["burger-solid-element"]}></span>
        </div>
        <div className={`${classes["header-list"]} ${headerState?classes["show"]:""}`}>
            <ul className={classes["links"]}>
                <Link onClick={closeHeader} className={classes["link-item"]} to={"/"}><li><span className={classes["link-underline"]}>Home</span></li></Link>
                <Link onClick={closeHeader} className={classes["link-item"]} to={"/projects"}><li><span className={classes["link-underline"]}>Projects</span></li></Link>
                <Link onClick={closeHeader} className={classes["link-item"]} to={"/contact-me"}><li><span className={classes["link-underline"]}>Contact Me</span></li></Link>
                {authCTX.isLoggedIn && (
                  <Link onClick={closeHeader} className={classes["link-item"]} to={"/dashboard"}><li><span className={classes["link-underline"]}>Dashboard</span></li></Link>
                )}
            </ul>
        </div>
      </header>
    </Fragment>
  );
};

export default Header;
