import React , { useState, useEffect } from "react"

const AuthContext = React.createContext({
    token : '',
    isLoggedIn : false,
    login : (token) => {},
    logout : () => {}
})

const retrieveStoredToken = () => {
    const token = localStorage.getItem("token")
    const storedExpTime = +localStorage.getItem("expirationTime")
    const remainingTime = calculateRemainingTime(storedExpTime)
    if(remainingTime < 2000) {
        localStorage.removeItem("token")
        localStorage.removeItem("expirationTime")
        return null
    }

    return {
        token,
        remainingTime
    }
}

const calculateRemainingTime = ( expirationTime ) => {
    const currentTime = new Date().getTime()
    const remainingTime = expirationTime -  currentTime
    return remainingTime
}

export const AuthContextProvider = (props) => {
    const tokenData = retrieveStoredToken()
    let initToken
    if(tokenData) {
        initToken = tokenData.token
    }
    const [ token, setToken ] = useState(initToken)
    const isUserLoggedIn = !!token;


    const logoutHandler = () => {
        setToken(null)
        localStorage.removeItem("token")
        localStorage.removeItem("expirationTime")
    }

    const loginHandler = (token) => {
        setToken(token)
        localStorage.setItem("token",token)
        const remainingTime = calculateRemainingTime(new Date().getTime() + 3600000)
        localStorage.setItem("expirationTime", new Date().getTime() + 3600000)
        setTimeout(logoutHandler,remainingTime)
    }

    useEffect(()=>{
        if(tokenData){
            console.log(tokenData.remainingTime)
            setTimeout(logoutHandler,tokenData.remainingTime)
        }
    },[tokenData])

    const contextValue = {
        token:token,
        isLoggedIn:isUserLoggedIn,
        login:loginHandler,
        logout:logoutHandler
    }

    return (
        <AuthContext.Provider value={contextValue}>{props.children}</AuthContext.Provider>
    )
}

export default AuthContext;