import React, { Suspense } from "react";
import { Route, Switch } from "react-router-dom";

import "./App.css";
import Header from "./components/core/header";
import AuthContext from "./store/authContexts";
import { useContext } from "react";
import Spinner from "./components/UI/spinner";

const LandingPage = React.lazy(() => import("./pages/landingPage"));
const ContactMe = React.lazy(() => import("./pages/contactMe"));
const Projects = React.lazy(() => import("./pages/projects"));
const Project = React.lazy(() => import("./pages/project"));
const Admin = React.lazy(() => import("./pages/admin/admin"));
const Dashboard = React.lazy(() => import("./pages/admin/dashboard"));
const AddTechnologyForm = React.lazy(() => import("./components/admin/addTechnologyForm"));
const AddProject = React.lazy(() => import("./components/admin/addProject"));
const EditProject = React.lazy(() => import("./components/admin/editProject"));
const Messages = React.lazy(() => import("./components/admin/messages"));
const Message = React.lazy(() => import("./components/admin/message"));
const NotFoundPage = React.lazy(() => import("./pages/notFoundPage"));

function App() {
  console.log("App initialized");
  const authCTX = useContext(AuthContext);
  return (
    <div className="App">
      <Header />
      <Suspense fallback={<Spinner />}>
        <Switch>
          <Route path={"/"} exact>
            <LandingPage />
          </Route>
          <Route path={"/contact-me"} exact>
            <ContactMe />
          </Route>
          <Route path={"/projects"} exact>
            <Projects />
          </Route>
          <Route path={"/projects/:projectId"}>
            <Project />
          </Route>
          {!authCTX.isLoggedIn && (
            <Route path={"/admin"} exact>
              <Admin />
            </Route>
          )}
          {authCTX.isLoggedIn && (
            <Route path={"/dashboard"} exact>
              <Dashboard />
            </Route>
          )}
          {authCTX.isLoggedIn && (
            <Route path={"/dashboard/technologies"}>
              <AddTechnologyForm />
            </Route>
          )}
          {authCTX.isLoggedIn && (
            <Route path={"/dashboard/projects"}>
              <AddProject />
            </Route>
          )}
          {authCTX.isLoggedIn && (
            <Route path={"/dashboard/edit-project/:projectId"}>
              <EditProject />
            </Route>
          )}
          {authCTX.isLoggedIn && (
            <Route path={"/dashboard/messages"} exact>
              <Messages />
            </Route>
          )}
          {authCTX.isLoggedIn && (
            <Route path={"/dashboard/messages/:msgId"}>
              <Message />
            </Route>
          )}
          <Route path={"*"} component={NotFoundPage} />
        </Switch>
      </Suspense>
    </div>
  );
}

export default App;
